.information{
  width: 100%;
  padding: 40px 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.information__time-timer{
  display: inline-block;
  line-height: 36px;
}

.information__time-text{
  vertical-align: bottom;
  display: inline-block;
  font-size: 20px;
  text-transform: uppercase;
  margin-left: 10px;
}

.information__time-strong{
  font-weight: var(--bold);
}

.information__discount-dot{
  display: inline-block;
  width: 22px;
  height: 22px;
  margin-right: 10px;
  background-color: var(--color-red);
  border-radius: 50%;
  text-align: center;
  line-height: 22px;
  color: var(--color-white);
  font-weight: var(--bold);
}

.information__discount-text{
  display: inline-block;
  font-size: 14px;
  font-weight: var(--bold);
  word-break: break-all;
  line-height: 20px;
}

@media (max-width: 1560px){
  .information{
    padding: 40px 20px;
  }
}

@media (max-width: 1100px){
  .information__discount{
    margin-top: 20px;
  }
}
@media (max-width: 690px){
  .information__quantity{
    margin-top: 20px;
    width: 100%;
  }
}

@media (max-width: 500px){
  .information{
    padding-top: 80px;
  }
}

@media (max-width: 390px){
  .information__time-text{
    font-size: 15px;
  }
}

